.product-plan {
    background-color: #d98282;
    padding: 5px;
    margin-bottom: 10px;
  }
  
  .feature-image {
    max-width: 25%;
    width: fit-content;
  }
  
  .product-plan img {
    max-width: 90%;
  }
  
  #change-buttons {
    margin-top: 10px;
    margin-left: 20px;
  }
  
  #change-buttons a {
    text-decoration: none;
    font-size: 1.5em;
    margin-right: 20px;
  }
  
  @media screen and (min-width: 600px) {
    .columns {
      display: flex;
    }
  }